import React from "react";
import { motion } from "framer-motion";

const processData = [
  {
    title: "Kontakt i darmowa wycena",
    icon: "fa-phone",
    text:
      "Specjalizujemy si w profesjonalnym malowaniu  dziaamy na terenie Wrocawia i okolic. Wypenij nasz formularz online lub zadzwo, a skontaktujemy si z Tob, by umwi dogodny termin spotkania. Zaufaj fachowcom od malowania wntrz i elewacji.",
  },
  {
    title: "Wizyta i finalizacja oferty",
    icon: "fa-house-circle-check",
    text:
      "Podczas spotkania na terenie Wrocawia i okolicach Wrocawia oceniamy stan techniczny powierzchni przeznaczonych do malowania, omawiamy zakres prac i przygotowujemy precyzyjn wycen naszych usug malarskich.",
  },
  {
    title: "Dobr kolorw",
    icon: "fa-palette",
    text:
      "Nie wiesz, jakie kolory wybra? Nasz zesp pomoe Ci dobra idealn palet barw do kadego wntrza  i nie tylko.",
  },
  {
    title: "Malujemy starannie i kompleksowo",
    icon: "fa-paint-roller",
    text:
      "Nie musisz si o nic martwi  przesuniemy meble, naprawimy drobne pknicia i starannie zabezpieczymy wszystko, czego nie trzeba malowa. Potem ruszamy do pracy!",
  },
  {
    title: "Twoja nieruchomo w nowych kolorach!",
    icon: "fa-border-all",
    text:
      "Nasz zesp dowiadczonych malarzy z Wrocawia realizuje kompleksowe usugi malarskie zgodnie z najwyszymi standardami  zarwno wewntrz, jak i na zewntrz budynkw. Gwarantujemy precyzj, czysto i trway efekt.",
  },
  {
    title: "Sprztanie i odbir prac",
    icon: "fa-brush",
    text:
      "Dbamy o Twoj wasno z najwysz starannoci. Po zakoczeniu malowania dokadnie sprztamy, tak aby Twoja nieruchomo bya w idealnym porzdku  poza wieo pomalowanymi cianami. Nastpnie wsplnie odbieramy i sprawdzamy jako wykonanych prac. Dokadne, precyzyjne i szybkie malowanie? We Wrocawiu to zadanie dla specjalistw z KolorSystem!",
  },
];

const Proces = () => {
  return (
    <div className="process" style={{overflowX:"auto"}}>
      <div className="container ui segment process-container">
        <div className="estimate-title-box">PRZEBIEG WSPPRACY</div>
        <hr className="line" />

        {processData.map((item, index) => (
          <motion.div
            className="process-box"
            key={index}
            initial={{
              opacity: 0,
              x: index % 2 === 0 ? -100 : 100,
            }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 1 + index * 0.2 }}
          >
            <p className="process-titles">
              <i className={`fa-solid ${item.icon}`}></i> {item.title}
            </p>
            <p className="process-p">{item.text}</p>
            {index !== processData.length - 1 && (
      <div className="arrow-down"><i className="fa-solid fa-arrow-down"></i></div>
    )}
          </motion.div>
        ))}
      </div>
    </div>
  );
};

export default Proces;
