import React from "react";
import { motion, useAnimation } from "framer-motion";
import { useEffect, useState } from "react";


import referencje from "../img/referencje.png";
import doswiadczenie from "../img/doswiadczenie.png";
import happyclient from "../img/happycliente.png";
import dostepnosc from "../img/dostepnosc.png";

const WhyUs = () => {
  const [isLargeScreen, setIsLargeScreen] = useState(window.innerWidth >= 992);

  useEffect(() => {
    const handleResize = () => setIsLargeScreen(window.innerWidth >= 992);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const motionVariants = (index) => {
    if (isLargeScreen) {
      // Na ekranach 992px: z dou lub z gry
      return {
        hidden: { opacity: 0, y: index % 2 === 0 ? 100 : -100 },
        visible: { opacity: 1, y: 0 },
      };
    } else {
      // Na ekranach <992px: z lewej lub z prawej
      return {
        hidden: { opacity: 0, x: index % 2 === 0 ? -100 : 100 },
        visible: { opacity: 1, x: 0 },
      };
    }
  };

  const data = [
    {
      img: doswiadczenie,
      title: "DOWIADCZENIE",
      alt: "Odznaka oznaczajca dowiadczenie",
      text: `Klienci z Wrocawia i okolic chtnie powierzaj nam kolejne zlecenia malarskie i polecaj nasze 
        prace swoim znajomym. Solidne wykoczenia wntrz, szybkie terminy realizacji oraz estetyczne malowanie cian 
        sprawiaj, e zaufao nam ju wiele osb`,
    },
    {
      img: happyclient,
      title: "REFERENCJE",
      alt: "Odznaka zadowolonego klienta",
      text: `Zadowoleni klienci z Wrocawia i okolic regularnie powierzaj nam kolejne zlecenia malarskie
        i chtnie polecaj nasze usugi znajomym. Profesjonalne malowanie mieszka i domw, ktre wykonujemy, 
        buduje nasz renom skuteczniej ni jakakolwiek reklama`,
    },
    {
      img: referencje,
      title: "PROFESJONALIZM",
      alt: "Odznaka profesjonalizmu",
      text: `Dziaamy z penym zaangaowaniem, realizujc najwysze standardy jakoci. Nasza praca przebiega zgodnie 
        z nowoczesnymi metodami zarzdzania projektami, a po wszystkim dokadnie sprztamy, by mg od razu cieszy 
        si czyst i gotow do uytku przestrzeni`,
    },
    {
      img: dostepnosc,
      title: "DOSTPNO",
      alt: "Odznaka oznaczajca dostpno",
      text: `Skontaktuj si z nami, opowiedz, co chcesz pomalowa, a my dopasujemy harmonogram tak,
        aby prace przebiegay sprawnie i bez zbdnego stresu. Szanujemy Twj czas!`,
    },
  ];

  return (
    <>
      <div className="why-us-title" style={{overflowX:"auto"}}>DLACZEGO MY?</div>
      <hr className="line" />
      <div className="ui container segment why-us-container">
        <div className="row why-us-row">
          {data.map((item, index) => (
            <motion.div
              key={index}
              className="col-sm-12 col-md-6 col-lg-3 ui segment why-us-segment"
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true, amount: 0.3 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
              variants={motionVariants(index)}
            >
              <span>
                <img
                  className="why-us-icons"
                  src={item.img}
                  alt={item.alt}
                />
              </span>
              <div className="why-us-titles">{item.title}</div>
              <hr className="line" />
              <p className="why-us-p">{item.text}</p>
            </motion.div>
          ))}
        </div>
      </div>
    </>
  );
};

export default WhyUs;
